export function generarPDF(nombre, telefono, vip) {
    const { jsPDF } = window.jspdf;
    const doc = new jsPDF({ orientation: "portrait", unit: "mm", format: "A5" });


    // Ruta de la imagen de fondo
    const bannerImgPath = require("./banners/diseo_PDF.png");

    // Crear un contenedor QR invisible
    let qrContainer = document.createElement("div");
    qrContainer.style.visibility = "hidden"; // Ocultarlo completamente

    document.body.appendChild(qrContainer); // Lo agregamos temporalmente al DOM

    // Generar QR
    new window.QRCode(qrContainer, {
        text: "THERAVE"+telefono ,
        width: 100,
        height: 100
    });

    // Esperar hasta que el QR se genere
    let checkQrImage = setInterval(() => {
        let qrImg = qrContainer.getElementsByTagName("img")[0];
        if (qrImg && qrImg.src) {
            clearInterval(checkQrImage); // Detener el chequeo
            document.body.removeChild(qrContainer); // Eliminar el QR del DOM
            procesarPDF(qrImg.src); // Generar el PDF con el QR
        }
    }, 100); // Revisar cada 100ms

    function procesarPDF(qrImgSrc) {
        loadImageToBase64(bannerImgPath, function (bannerImg) {
            doc.addImage(bannerImg, "JPEG", 0, 0, 150, 210);

            //  Informacin del asistente
            doc.setFontSize(10);
            doc.setFont("helvetica", "bold");
            doc.text("Fecha del evento: ", 35, 43);
            doc.setFont("helvetica", "normal");
            doc.text("19 de julio de 2025 a partir de 23:30", 40 + doc.getTextWidth("Fecha del evento:"), 43);

            doc.setFont("helvetica", "bold");
            doc.text("Nombre y Apellidos:", 35, 51);
            doc.setFont("helvetica", "normal");
            doc.text(nombre, 40 + doc.getTextWidth("Nombre y Apellidos:"), 51);

            doc.setFont("helvetica", "bold");
            doc.text("Telfono:", 35, 59);
            doc.setFont("helvetica", "normal");
            doc.text(telefono, 40 + doc.getTextWidth("Telfono:"), 59);

            doc.setFont("helvetica", "bold");
            doc.text("Vip:", 35, 67);
            doc.setFont("helvetica", "normal");
            doc.text(vip, 36 + doc.getTextWidth("Telfono:"), 67);


            // Agregar el QR al PDF
            doc.addImage(qrImgSrc, "JPEG", 65, 90, 50, 50);

            // Cdigo nico al pie
            doc.setFontSize(10);
            doc.setFont("helvetica", "normal");
            doc.setTextColor(0, 150, 136);
            doc.text("THERAVE"+telefono, 72, 145);

            //  Condiciones del evento
            doc.setFontSize(8);
            doc.setFont("helvetica", "normal");
            doc.setTextColor(33, 33, 33);
            const condiciones = [
                "EVENTO PARA MAYORES DE 15 AOS. Permite el acceso al recinto.",
                "Menores de 15, 16 y 17 aos solo con aprobacin por Padre/Madre/Tutor",
                "al responsable del evento."
            ];
            let yPos = 170;
            condiciones.forEach(line => {
                const textWidth = doc.getTextWidth(line);
                const xPos = Math.max((210 - textWidth) / 87, 87);
                doc.text(line, xPos, yPos, { maxWidth: 190, align: "center" });
                yPos += 8;
            });

            //  Descargar PDF
            doc.save(`Entrada_${nombre.replace(/\s+/g, "_")}.pdf`);
        });
    }

    function loadImageToBase64(url, callback) {
        const img = new Image();
        img.crossOrigin = "Anonymous";
        img.src = url;
        img.onload = function () {
            const canvas = document.createElement("canvas");
            canvas.width = img.width;
            canvas.height = img.height;
            const ctx = canvas.getContext("2d");
            ctx.drawImage(img, 0, 0);
            callback(canvas.toDataURL("image/png"));
        };
        img.onerror = function () {
            console.error("Error al cargar la imagen:", url);
        };
    }
}
