// Variable global para guardar el resultado escaneado del QR
let scannedQRData: string | null = null;

// Setter para guardar un valor nuevo
export const setScannedQRData = (data: string) => {
  scannedQRData = data;
};

// Getter para obtener el valor actual
export const getScannedQRData = (): string | null => {
  return scannedQRData;
};